body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h3{
    text-align: center;
    font-size: 50px!important;
    margin:0;
    padding-top:150px!important;
    font-weight: bold!important;
    color: red!important;
}

/* .image{
    height: 575px;
} */
.card-image{
    background-size: "cover";
}

.article{
    color: rgb(252, 248, 248);
    background-color: rgb(71, 122, 124);
    height: 500px;
    opacity: 0.9;
    padding: 20px;
}
h1{
    color: rgb(236, 30, 30);
    font-weight: 400!important;
    font-size: 20px;
    text-align: center;
    margin-top: 40px;
    margin-bottom: 50px;
}
hr{
    margin-top: 25px!important;
}

.sociaux{
    font-size: 30px;
    padding: 10px;
}
.page-footer{
    margin-top: 20px;
}

.container-fluid{
  background-image: url('/img/back_yellowgreen.jpeg');
  background-attachment: fixed;
  background-size: cover;
}
.slogo{
  margin-bottom: 5px;
}
.logo{
  z-index: 99;
  position: absolute;
  float: left;
  margin-top: 20px;
  margin-left:20px;
  color: red;
  font-size: 20px;
  font-weight: bold;
}
.slogan{
  z-index: 98;
  position: absolute;
  margin-top: 90px;
  margin-left:20px;
  float: left;
  color: green;
  font-size: 20px;
  font-style: italic;
}

.reseau{
  z-index: 100;
  position: absolute;
  width:5px;
}

h1, h2, p, ul, li {
  font-family: sans-serif;
}

ul.menu li {
  display: inline;
  list-style-type: none;
  margin: 0;
}
ul.menu {
  background-color: rgb(75, 46, 9);
  padding: 0;
  opacity: 0.8;
}
ul.menu li a {
  color: #FFF;
  font-size: 18px;
  font-weight: 400;
  text-decoration: none;
  padding: 25px;
  display: inline-block;
}

.content h2 {
  padding: 0;
  margin: 0;
}
.content li {
  margin-bottom: 10px;
}
.active:hover {
  background-color: rgb(130, 177, 0);
}


